import React, { Component } from 'react';

class Weather extends Component {

  constructor(props) {
    super(props);
    this.state = {
      stationName: '',
      weather: {},
      warnings: [],
      updatedAt: null
    }
  }

  componentWillReceiveProps(nextProps){
    let tenMinutesAgo = new Date(Date.now() - 600000);
    if(this.state.updatedAt === null || this.state.updatedAt < tenMinutesAgo){
      this.loadWeather();
    }
  }

  render() {
    return (
      <div className="weather-pane">
        <div>{this.state.stationName}</div>
        <div>{this.state.weather.windSpeed} km/hr {this.state.weather.windDir}</div>
      </div>
    )
  }

  loadWeather(){
    var that = this;
    fetch('https://2vlomosbqb.execute-api.ap-southeast-2.amazonaws.com/v1/airportWeather?station_id=YSSY', {
      cache: 'no-store'
    })
    .then(function(response) {
      return response.json();
    })
    .then(function(response) {
      that.setState({
        stationName: response.station,
        weather: response.weather,
        warnings: response.warnings,
        updatedAt: new Date()
      });
    }).catch(function(error) {
      console.log('Fetch Weather Request Failed ' + error);
    });
  }

}

export default Weather;
