import React, { Component } from 'react';

class FlightListing extends Component {
  render() {
    let altitude = this.props.aircraft.flight.altitude === 'ground' ? 'Ground' : (this.props.aircraft.flight.altitude !== null ? this.props.aircraft.flight.altitude + 'ft' : '');
    let userToAircraft = this.props.aircraft.userToAircraft.distance ? (this.props.aircraft.userToAircraft.distance/1000).toFixed(1) + 'km ' + this.props.aircraft.userToAircraft.direction : '';
    let flightClassNames = 'flight-item';
    if(this.props.highlight === this.props.aircraft.flight.icao){
      flightClassNames += ' highlight';
    }
    return (
      <div className={flightClassNames} onClick={() => this.props.aircraftMarkerClickHandler(this.props.aircraft.flight.icao)}>
        <div className="flight-details">
          <div className="airline-icon">
            <img src={this.airlineLogo()} alt="airline" style={{width: '38px', height: '38px'}}/>
          </div>
          <div className="flight-identity">
            <span className="title">{this.props.aircraft.flight.flight || this.props.aircraft.flight.icao}</span>
            <span className="subtitle">{this.props.aircraft.aircraft.typeName}</span>
          </div>
          <div className="proximity">{userToAircraft}</div>
        </div>
        {this.props.aircraft.origin.icao != null &&
        <div className="route">
          <div className="location origin">
            <span className="title">{this.props.aircraft.origin.airportName}</span>
            <span className="subtitle">{this.props.aircraft.origin.icao}</span>
          </div>
          <div className="route-progress">
            <div className="progress">
              <div className="progress-bar" style={{width: '25%'}}></div>
            </div>
          </div>
          <div className="location destination">
            <span className="title">{this.props.aircraft.destination.airportName}</span>
            <span className="subtitle">{this.props.aircraft.destination.icao}</span>
          </div>
        </div>
        }
        <div className="metrics">
          <div>
            <span className="title">Altitude</span>
            <span className="subtitle">{altitude}</span>
          </div>
          <div>
            <span className="title">Speed</span>
            <span className="subtitle">{this.props.aircraft.flight.groundSpeed ? this.props.aircraft.flight.groundSpeed + 'kts' : ''}</span>
          </div>
          <div>
            <span className="title">Vert Rate</span>
            <span className="subtitle">{this.props.aircraft.flight.verticalRate ? this.props.aircraft.flight.verticalRate + 'ft/min' : ''}</span>
          </div>
          <div>
            <span className="title">Course</span>
            <span className="subtitle">{this.props.aircraft.flight.track} &deg;</span>
          </div>
        </div>
      </div>
    );

  }

// import icon from './images/favicon.ico';
  airlineLogo(){
    switch(this.props.aircraft.airline.shortName) {
      case 'Qantas': {
        return process.env.PUBLIC_URL + '/airlines/qantas.jpg';
        break;
      }
      case 'Jetstar': {
        return process.env.PUBLIC_URL + '/airlines/jetstar.jpg';
        break;
      }
      case 'Virgin Australia': {
        return process.env.PUBLIC_URL + '/airlines/virgin.jpg';
        break;
      }
      case 'Tigerair Australia': {
        return process.env.PUBLIC_URL + '/favicon.ico';
        break;
      }
      case 'Emirates': {
        return process.env.PUBLIC_URL + '/airlines/emirates.jpg';
        break;
      }
      case 'Singapore Airlines': { //unconfirmed
        return process.env.PUBLIC_URL + '/airlines/singapore.jpg';
        break;
      }
      case 'Scoot': { //unconfirmed
        return process.env.PUBLIC_URL + '/airlines/scoot.jpg';
        break;
      }
      default: {
        return process.env.PUBLIC_URL + '/favicon.ico';
        break;
      }
    }
  }

}
export default FlightListing;
